.Clock {
  cursor: pointer;
}

.Clock-Time {
  font-size: 17rem;
  display: flex;
  flex-direction: row;
}

.Clock-Digits {
  display: flex;
  flex-direction: row;
  margin: 0px 2rem;
}

.Clock-Digit{
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}

.Clock-Digit.Large {
  width: 9.5rem;
}

.Clock-Digit-Half {
  font-size: 6.25rem;
  /* height: 6rem; */
}

.Clock-Date {
  margin-bottom: 10pt;
}

.lds-roller {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  margin : 30px;
}
.lds-roller div {
  -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  -webkit-transform-origin: 40px 40px;
          transform-origin: 40px 40px;
}
.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background: #fff;
  margin: -4px 0 0 -4px;
}
.lds-roller div:nth-child(1) {
  -webkit-animation-delay: -0.036s;
          animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
  top: 63px;
  left: 63px;
}
.lds-roller div:nth-child(2) {
  -webkit-animation-delay: -0.072s;
          animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
  top: 68px;
  left: 56px;
}
.lds-roller div:nth-child(3) {
  -webkit-animation-delay: -0.108s;
          animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
  top: 71px;
  left: 48px;
}
.lds-roller div:nth-child(4) {
  -webkit-animation-delay: -0.144s;
          animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
  top: 72px;
  left: 40px;
}
.lds-roller div:nth-child(5) {
  -webkit-animation-delay: -0.18s;
          animation-delay: -0.18s;
}
.lds-roller div:nth-child(5):after {
  top: 71px;
  left: 32px;
}
.lds-roller div:nth-child(6) {
  -webkit-animation-delay: -0.216s;
          animation-delay: -0.216s;
}
.lds-roller div:nth-child(6):after {
  top: 68px;
  left: 24px;
}
.lds-roller div:nth-child(7) {
  -webkit-animation-delay: -0.252s;
          animation-delay: -0.252s;
}
.lds-roller div:nth-child(7):after {
  top: 63px;
  left: 17px;
}
.lds-roller div:nth-child(8) {
  -webkit-animation-delay: -0.288s;
          animation-delay: -0.288s;
}
.lds-roller div:nth-child(8):after {
  top: 56px;
  left: 12px;
}
@-webkit-keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

a {
  color: inherit;
}

.Weather {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Weather-Input {
  margin: 5px;
  padding: 5px 20px;
  font-size: 24pt;
  background-color: inherit;
  border: 2px solid white;
  color: white;
  border-radius: 30px;
  font-family: Quicksand;
}

input.Weather-Input:focus {
  outline: none;
}

.WeatherDisplay {
  display: flex;
  max-width: 1000px;
  min-height: 25vh;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.FutureWeather {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 125px;
  /* border: 1pt solid white; */
  margin: 2rem 0.25rem;
  border-radius: 10px;
  padding: 5px 0px;
  font-size: 22px;
  cursor: pointer;
}

.Weather-Icon {
  text-align: center;
  margin: 15px 0px;
}

.Weather-Icon.Future {
  font-size: 60px;
}

.Weather-Icon.Current {
  font-size: 80px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Weather-Location {
  cursor: pointer;
}

.Weather-Alert {
  position: absolute;
  z-index: 1;
  background-color: inherit;
  border-radius: 50%;
  padding: 2px;
  color: #b71c1c;
}

.Weather-Alert.Weather {
  font-size: 25pt;
  left: -5px;
  top: -15px;
}

.Weather-Alert.Transit {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16pt;
  border: 2pt solid #b71c1c;
  padding: 3.5px;
  left: 90px;
  top: -15px;
  min-width: 20px;
  min-height: 20px;
}

.Weather-Temperature {
  width: 100px;
  margin: 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.Weather-LowTemp {
  opacity: 0.5;
}

.Weather-TempDisplay {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Weather-TempDisplay > i {
  font-size: 24pt;
}

.Weather-Display-SmallText {
  margin: 10px 0px;
  font-size: 14pt;
}

.CurrentWeather {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 165px;
  /* border: 1pt solid white; */
  font-size: 36px;
  margin: 2rem 0rem;
  border-radius: 10px;
  padding: 5px 0px;
  cursor: pointer;
}

.Frame {
  position:fixed;
  bottom: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
}

.IconsAndButtons {
  margin: 1rem;
  padding-left: 1rem;
  z-index: 3;
  position: fixed;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  /* border: 1pt solid white; */
  border-radius: 2rem;
}

.IconsAndButtons-ColorPicker {
  margin: 0 1rem;
}

.Settings {
  height: 100%;
  display: flex;
  align-items: flex-end;
}

.Settings-Icon {
  font-size: 24pt;
  padding: 1rem;
  cursor: pointer;
}

.Logos {
  /* padding: 2rem; */
  display: flex;
  flex-direction: column;
  /* border: 1pt solid white; */
}

.Logos-Icon {
  width: 10rem;
  margin: 5pt;
  font-size: 30pt;
}

.CatGif {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  padding: 1rem;
}

.CatGif > img {
  border-radius: 20px;
  height: 6rem;

}

.App {
  text-align: center;
}

.App-Content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  /* background-color: red; */
  /* color: black; */
  font-size: 18pt;
}

